import { IGameObject } from './game-object.interface';

export class GameObject implements IGameObject {
    public x: number;
    public y: number;
    public width: number;
    public height: number;
    public speed = 1;
    constructor(x: number, y: number, w: number, h: number) {
        this.x = x;
        this.y = y;
        this.width = w;
        this.height = h;
    }
    public setX = (x: number) => this.x = x;
    public setY = (y: number) => this.y = y;
    public setWidth = (w: number) => this.width = w;
    public setHeight = (h: number) => this.height = h;

    public getX = () => this.x;
    public getY = () => this.y;
    public getWidth = () => this.width;
    public getHeight = () => this.height;
}
